import React, { useState } from "react";
import "./Body.css";
const Body = () => {
  const [result, setresult] = useState(0);
  const [count, setcount] = useState(1);
  let cards = new Array([]);

  ShowCards();
  function ShowCards() {
    cards.length = 0;
    for (let i = 0; i < count; i++) {
      cards.push(
        <div key={i}>
          <input placeholder="Subject Name" />
          <select id={`grade${i}`} name="grade">
            <option value="0">GRADE</option>
            <option value="4"> A </option>
            <option value="3.75">A-</option>
            <option value="3.5">B+</option>
            <option value="3">B</option>
            <option value="2.5">C+</option>
            <option value="2">C</option>
            <option value="1.5">D+</option>
            <option value="1">D</option>
            <option value="0">F</option>
          </select>
          <select id={`ch${i}`} name="creditHours">
            <option value="0">Credits</option>
            <option value="4">4</option>
            <option value="3">3</option>
            <option value="2">2</option>
            <option value="1">1</option>
          </select>
          <i className="fas fa-times" onClick={(e) => setcount(count - 1)}></i>
        </div>
      );
    }
  }
  const Results = () => {
    let totalCH = 0;
    let totalGrades = 0;
    let totalRes = 0.0;
    for (let i = 0; i < count; i++) {
      let idOfCh = `ch${i}`;
      let idOfGrade = `grade${i}`;
      let ch = parseFloat(document.getElementById(idOfCh).value);
      totalCH = ch + totalCH;
      totalGrades = parseFloat(document.getElementById(idOfGrade).value);
      totalRes = totalRes + parseFloat(ch * totalGrades);
    }
    setresult(totalRes / totalCH);
  };

  return (
    <div>
      <div className="container">
        <h1>Gpa Calculator</h1>
      </div>

      <div className="container-body">
        <h2>Semester</h2>
        <div>
          <form>{cards}</form>
          <button onClick={Results} type="submit">
            Calculate
          </button>
        </div>
        <button onClick={(e) => setcount(count + 1)}>Add Subject</button>

        <div className="results">
          <h4>Your GPA is : {result}</h4>
        </div>
      </div>
    </div>
  );
};

export default Body;
